package by.blackstone.mine.never

import dev.icerock.moko.mvvm.viewmodel.ViewModel
import kotlinx.coroutines.flow.MutableStateFlow
import kotlinx.coroutines.flow.update

data class AppUiState(
    val rows: List<String> = emptyList(),
)

class AppViewModel : ViewModel() {

    val uiState = MutableStateFlow(AppUiState())

    fun readFilesContent(contents: List<String>) {
        contents.firstOrNull()?.let { content ->
            val split = content.lines()
                .filter { it.contains("Добыты ресурсы") }
                .map { it.replaceHtml() }

            uiState.update { it.copy(rows = split) }
        }
    }
}

//<font class=chattime>&nbsp;01:04:30&nbsp;</font><SPAN class=massm>&nbsp;iBClient&nbsp;</SPAN> <FONT color=#000000>
//Добыты ресурсы (уровень <b>1</b>, клетка <b>10</b>x<b>18</b>): <b>Оловянная руда (0.3), Медная руда (1.3), Сера (0.4)</b>.</FONT><br>

private fun String.replaceHtml(): String {
    val lastIndexTimer = lastIndexOf("Добыты ресурсы")
    return this.substring(lastIndexTimer)
        .replace("<b>", "")
        .replace("</b>", "")
        .replace(".</FONT><br>", "")
}