import androidx.compose.runtime.getValue
import androidx.compose.runtime.mutableStateOf
import androidx.compose.runtime.remember
import androidx.compose.runtime.rememberCoroutineScope
import androidx.compose.runtime.setValue
import androidx.compose.ui.ExperimentalComposeUiApi
import androidx.compose.ui.window.CanvasBasedWindow
import by.blackstone.mine.never.App
import by.blackstone.mine.never.AppViewModel
import by.blackstone.mine.never.picker.FilePicker
import kotlinx.coroutines.launch
import org.jetbrains.skiko.wasm.onWasmReady

@OptIn(ExperimentalComposeUiApi::class)
fun main() {
    onWasmReady {
        CanvasBasedWindow("MineNever") {
            val scope = rememberCoroutineScope()
            val viewModel = remember { AppViewModel() }
            var isShowFilePicker by remember { mutableStateOf(false) }

            App(viewModel = viewModel) {
                isShowFilePicker = true
            }

            FilePicker(isShowFilePicker, fileExtensions = listOf("html"), initialDirectory = null) { files ->
                scope.launch {
                    val contents = files.map { it.getFileContents()  }
                    viewModel.readFilesContent(contents)
                }
                isShowFilePicker = false
            }
        }
    }
}
