package by.blackstone.mine.never

import androidx.compose.foundation.background
import androidx.compose.foundation.layout.Column
import androidx.compose.foundation.layout.Row
import androidx.compose.foundation.layout.WindowInsets
import androidx.compose.foundation.layout.fillMaxHeight
import androidx.compose.foundation.layout.fillMaxSize
import androidx.compose.foundation.layout.fillMaxWidth
import androidx.compose.foundation.layout.padding
import androidx.compose.foundation.layout.safeDrawing
import androidx.compose.foundation.layout.windowInsetsPadding
import androidx.compose.foundation.lazy.LazyColumn
import androidx.compose.foundation.lazy.LazyListState
import androidx.compose.foundation.lazy.items
import androidx.compose.foundation.lazy.rememberLazyListState
import androidx.compose.material3.Button
import androidx.compose.material3.Text
import androidx.compose.runtime.Composable
import androidx.compose.runtime.collectAsState
import androidx.compose.runtime.getValue
import androidx.compose.ui.Modifier
import androidx.compose.ui.graphics.Color
import androidx.compose.ui.unit.dp
import by.blackstone.mine.never.theme.AppTheme
import by.blackstone.mine.never.ui.scrollbar

@Composable
fun App(
    viewModel: AppViewModel,
    onShorFilePicker: () -> Unit,
) = AppTheme {

    val state by viewModel.uiState.collectAsState()
    val lazyListState: LazyListState = rememberLazyListState()

    Column(
        modifier = Modifier.fillMaxSize().windowInsetsPadding(WindowInsets.safeDrawing)
    ) {
        Row(modifier = Modifier.fillMaxSize()) {
            Column(
                modifier = Modifier.weight(0.7f).fillMaxHeight().background(Color.Red.copy(alpha = .1f))
            ) {

                LazyColumn(
                    state = lazyListState,
                    modifier = Modifier
                        .fillMaxWidth()
                        .scrollbar(
                            state = lazyListState,
                            horizontal = false
                        )
                ) {
                    items(state.rows) { item ->
                        Text(modifier = Modifier.padding(16.dp), text = item)
                    }
                }
            }

            Column(
                modifier = Modifier.weight(0.3f).fillMaxHeight().background(Color.Blue.copy(alpha = .1f))
            ) {

                Button(onShorFilePicker) {
                    Text(text = "Picker")
                }
            }
        }

    }
}

internal expect fun openUrl(url: String?)